<template>
	<view style="overflow: hidden;">
		<!--帖子详情页详情信息-->
		<commonList :item="info" isdetail
		@doSupport="doSupport"
		@follow="follow" @doComment="doComment" @doShare="doShare">
			<view class="">
				{{info.content}}
			</view>
			<view class="">
				<image v-for="(item,index) in info.images" @click="preview(index)"
				:src="item.url" class="w-100" mode="widthFix"></image>
			</view>
		</commonList>
		
		<divider></divider>
		

		<view class="p-2 font-md font-weight-bold">
			最新评论 3
		</view>
		<view class="p-2  uni-comment-list" >
			<view class="uni-comment-face">
				<image src="../../static/default.jpg" class="rounded-0" mode="widthFix"></image>
			</view>
			<view class="uni-comment-body">
				<view class="uni-comment-top">
					<text>小秘密</text>
				</view>
				<view class="uni-comment-content">国产货</view>
				<view class="uni-comment-date">
					<view class="">2天前</view>
					<view class="uni-comment-replay-btn">
						5回复
					</view>
				</view>
			</view>
		</view>
		
		
		<view class="" style="height: 100rpx;"></view>
		<!--发送框-->
		<bottomInput @submit="submit"></bottomInput>
		
		<moreShare ref="share" :title="info.title" :shareText="info.content" :image="info.titlepic"></moreShare>
	</view>
</template>

<script>
	import commonList from '../../components/common/common_list.vue';
	import bottomInput from '../../components/common/bottom-input.vue';
	import moreShare from '../../components/common/more-share.vue'
	export default {
		components:{
			commonList,
			bottomInput,
			moreShare
		},
		data() {
			return {
				// 当前帖子信息
				info : {
					username: '昵称',
					userpic: '../../static/default.jpg',
					newstime: '2019-10-20 下午04:30',
					isFollow: false,
					title: '我是标题',
					titlepic: 'https://tangzhe123-com.oss-cn-shenzhen.aliyuncs.com/Appstatic/qsbk/demo/datapic/4.jpg',
					support: {
						type: 'support',
						support_count: 1,
						unsupport_count: 0
					},
					comment_count: 2,
					share_num: 2,
					content : "uniapp 社区交友",
					images:[{
						url : "https://tangzhe123-com.oss-cn-shenzhen.aliyuncs.com/Appstatic/qsbk/demo/datapic/4.jpg"
					},{
						url : "https://tangzhe123-com.oss-cn-shenzhen.aliyuncs.com/Appstatic/qsbk/demo/datapic/5.jpg"
					}]
				}
			}
		},
		onLoad(e) {
			// 初始化
			if(e.detail){
				this._init(JSON.parse(e.detail))
			}
		},
		computed:{
			imagesList(){
				return this.info.images.map(item => item.url)
			}
		},
		onNavigationBarButtonTap() {
			this.$refs.share.open()
		},
		onBackPress() {
			this.$refs.share.close()
		},
		methods: {
			_init(data){
				// 修改标题
				uni.setNavigationBarTitle({
					title : data.title
				})
				// 请求api
			},
			// 评论
			doComment(){},
			// 分享
			doShare(){},
			// 关注
			follow(){
				this.info.isFollow = true
				uni.showToast({
					title : '关注成功'
				})
			},
			// 顶踩操作
			doSupport(e){
				let item = this.info;
				if(this.info.support.type === e.type){
					return uni.showToast({
						title : '你已经操作过了',
						icon : 'none'
					})
				}
				let msg = e.type === "support" ? "点赞" : "反对";
				if (item.support.type === '') {
					item.support.type = e.type;
					item.support[e.type + '_count']++;
				}
				if (item.support.type === 'support' && e.type === 'unsupport') {
					item.support.support_count--;
					item.support.unsupport_count++;
				} else if (item.support.type === 'unsupport' && e.type === 'support') {
					item.support.support_count++;
					item.support.unsupport_count--;
				}
				item.support.type = e.type;
			},
			// 预览功能
			preview(index){
				// 预览图片
				uni.previewImage({
					current:index,
					urls: this.imagesList,
				});
			},
			// 提交
			submit(data){
				console.log(data)
			}
		}
	}
</script>

